import React, { Component } from 'react';
import Navbar from '../../components/nav';
import Community from '../../components/community';
import { connect } from 'react-redux';
import { getFeeds, getFeedsAuth } from '../../redux/actions/feedActions';

//getFeeds for unAuth user
//getFeedsAuth for Auth user
//getComments for comments of specific feed
//getReplies for getting replies of a specific comment of a specific field
//regusterFeeds adds a feed
//registerComments adds a comment to a specific field
//register Replies adds a comment to a specific comment in a specific field

//Error might occur do tell

class index extends Component {

    componentDidMount() {
        const { id } = this.props.match.params;
        const { Auth, profile } = this.props;
        var data;
        if (id) {
            data = { limit: 0, _id: id };
            this.props.getFeedsAuth(data);
        }
        else  {
            console.log('tried')
            data = { limit: 0 }
            this.props.getFeeds(data)
        }
        //id null get feed else get feed auth
        //here always send value 0 but when working on show more send 1
    }

    render() {
        const { Auth, profile } = this.props;
        //RegisterFeed.isLoading   //Feed.isLoading
        //RegisterFeed.isError      //Feed.isError use Feed.err
        //RegisterFeed.isSuccess    //Feed.isSuccess use Feed.data
        return (
            <div className="container-fluid p-0" style={{ minHeight: '90vh', display: 'flex', flexDirection: 'column' }}>
                <Navbar active={1} />
                <Community auth={Auth} profile={profile} />
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        Auth: state.Auth.auth,
        profile: state.Profile.data,
        isError: state.Profile.isError,
        RegisterFeed: state.RegFeed,
    }
}

export default connect(mapStateToProps,{getFeeds,getFeedsAuth})(index);;